@import url(https://fonts.googleapis.com/css2?family=Gelasio:ital,wght@0,400;0,700;1,400&display=swap);

* {
    margin: 0;
    padding: 0;
    box-sizing: border-box;
}




.banner {
    display: flex;
    align-items: center;
    padding: 10px 30px;
    background-color: rgba(255, 188, 0, 1);
    background-color: var(--main-yellow);
    width: 100%;
    align-self: center;
    text-align: center;
    justify-self: center;
    position: fixed;
    font-size: 12px;
    top: 0;
    font-family: monospace;
    font-size: 1.1rem;
    z-index: 1;
    transition: 0.5s all linear;
}

.banner.inactive {
    top: -6em;
    opacity: 0;
}
.banner a {
text-decoration: underline;
font-weight: bold;
}
.banner a:hover, .banner img:hover {
    color: black;    
    text-decoration: underline;
    font-weight: bold;
    -webkit-filter: invert(100%) sepia(1%) saturate(0%) hue-rotate(32deg) brightness(102%) contrast(101%);
            filter: invert(100%) sepia(1%) saturate(0%) hue-rotate(32deg) brightness(102%) contrast(101%);
    }
.banner img {
    height: 20px;
    margin-left: 10px;
    cursor: pointer;
}
:root {
    --main-yellow: rgba(255, 188, 0, 1);
    --grey: #F7F7F7;
    --main-font: 'Gelasio', sans-serif;
    --secondary-font: 'Noto Sans', sans-serif;
    --biggest-category: normal bold 1.2rem /1.2 var(--secondary-font);
    --biggest-heading: bold 2.3rem /2.4rem var(--main-font);
    --biggest-content: 1.14rem /1.7rem var(--main-font);
    --biggest-meta: italic 0.8rem var(--main-font);
    --large-content: normal 1.1rem /1.8rem var(--main-font);
    --large-category: normal bold 1.2rem /1.2 var(--secondary-font);
    --largest-category: normal bold 1.14rem /1.2 var(--secondary-font);
    --small-bold: normal bold 0.9rem /1.2 var(--secondary-font);
    --medium-heading: bold 1.85rem /1.9rem var(--main-font);
    --medium-content: 0.95rem /1.4rem var(--main-font);
    --medium-meta: italic 0.78rem var(--main-font);
    --medium-bold: 1rem;
    --small-heading: bold 1.2rem /1.5rem var(--main-font);
    --small-meta: italic 0.750rem /1rem var(--main-font);
    --large-noto: normal bold 1.18rem /2.4rem var(--secondary-font);
    --small-noto: normal 1rem /2.4rem var(--secondary-font);
    --small-noto-bold: bold 1rem /1.4rem var(--secondary-font);
    --tiny: normal 0.71rem /1.4rem var(--secondary-font);
    --main-padding: 1.72rem;
}
body {
    display: grid;

    justify-content: center;

}
a {
    text-decoration: none;
    color: black;
    background-color: none;
    transition: all 0.5s;
}
a:hover {
    color: rgba(255, 188, 0, 1);
    color: var(--main-yellow);
}
.App {
    display: flex;
    flex-direction: column;
    margin: 1.72rem 0;
    margin: var(--main-padding) 0;
    align-self: center;
    place-items: center;
    max-width: 1440px;
}

.App-header {
    display: grid;
    grid-template-columns: 1fr 1fr;
    grid-template-areas: "img div" "img svg";
    padding: 0 1.72rem;
    padding: 0 var(--main-padding);
    margin-bottom: 2rem;
    width: 100%;
}

.App-header img {
    grid-area: img;
    align-self: center;
}

.App-logo-footer {
    display: none;
}

.App-header div {
    grid-area: div;
    display: flex;
    justify-self: right;
    background-color: rgba(255, 188, 0, 1);
    background-color: var(--main-yellow);
    padding: 3px 10px;
    font-weight: bold;
    font-size: 12px;
    justify-self: right;
    align-self: center;
    font-family: Noto Sans, sans-serif;
}

.App-header div p {
    align-items: center;
    justify-content: center;
}

.App-header svg {
    grid-area: svg;
    height: 15px;
    justify-self: right;
    align-self: center;
}

.article-big {
    display: flex;
    flex-direction: column;
    align-items: center;
    margin: 30px 0px;
    margin: 0 1.72rem;
    margin: 0 var(--main-padding);
    margin-bottom: 1.1rem;
}

.category {
    align-self: start;
    border-bottom: 2px solid rgba(255, 188, 0, 1);
    border-bottom: 2px solid var(--main-yellow);
    transition: all 0.5s;
    justify-content: right;
    margin: 1rem 0rem;
    font: normal bold 0.9rem /1.2 'Noto Sans', sans-serif;
    font: var(--small-bold);
}
.category p {
    transition: all 0.5s;
}

.category :hover{
    
    padding-left: 1.1rem;
}

.category:hover {
    cursor: pointer;
    border-bottom: 2px solid black;
}

h2 {

    font: bold 1.85rem /1.9rem 'Gelasio', sans-serif;

    font: var(--medium-heading);
    margin-bottom: 1rem;
    transition: color 0.5s;
    text-align: left;
    width: 100%;
}

h2:hover {
    color: rgba(255, 188, 0, 1);
    color: var(--main-yellow);
    cursor: pointer;
}

.article-big .summary {
    margin-bottom: 1rem;
    font: 0.95rem /1.4rem 'Gelasio', sans-serif;
    font: var(--medium-content);
}

.article-big:first-of-type .category:hover {
    padding: 0px 14px 7px 14px;
    border-color: black;
}

.article-big:first-of-type .category {
    align-self: center;
    font: normal bold 1.2rem /1.2 'Noto Sans', sans-serif;
    font: var(--biggest-category);
    border-bottom: 5px solid rgba(255, 188, 0, 1);
    border-bottom: 5px solid var(--main-yellow);
}

.article-big:first-of-type h2 {
    font: bold 2.3rem /2.4rem 'Gelasio', sans-serif;
    font: var(--biggest-heading);
    text-align: center;
    margin-bottom: 1rem;
}

.article-big:first-of-type .summary {
    font-size: 1.18rem;
    font: 1.14rem /1.7rem 'Gelasio', sans-serif;
    font: var(--biggest-content);
    text-align: center;
}

.article-big:first-of-type .category {
    align-self: center;
    padding-bottom: 7px;
    margin-bottom: 1.5rem;
}

.article-big:first-of-type .article-meta {
    font: italic 0.8rem 'Gelasio', sans-serif;
    font: var(--biggest-meta);
    text-align: center;
}

/*
.article-big div figure  {
    display: flex;
    position: relative;
    width: 100%;
    padding-bottom: 75%;
    overflow: hidden;
    justify-content: center;
}
*/

.article-big div figure img {
    position: absolute;
    left: 50%;
    top: 50%;
    height: 100%;
    width: 100%;
    object-fit: cover;
    transform: translate(-50%, -50%);
}

.article-big .lazyload-wrapper {
    position: relative;
    width: 100%;
    padding-bottom: 75%;
    justify-content: center;
    overflow: hidden;
    padding-bottom: calc(0.0rem + (55vw));
    margin-bottom: 0rem;
}

.article-big:first-of-type .lazyload-wrapper {
    padding-bottom: calc(23rem + (6.5vw));
    margin-bottom: 3rem;
}

img {
    object-position: 50% 10%;
    transition: all 0.5s;
}

figure:hover {
    cursor: pointer;
}

figure img:hover {
    -webkit-filter: grayscale(100%);
            filter: grayscale(100%);
}

.article-meta {
    font-style: italic;
    font-size: 0.79rem;
    font: italic 0.78rem 'Gelasio', sans-serif;
    font: var(--medium-meta);
    width: 100%;
}

.article-meta a {
    text-decoration: none;
    color: black;
}

.article-meta a:hover {
    cursor: pointer;
    color: grey;
}

.article-meta time::before {
    content: '|';
    margin: 0px 0.3rem;
}

.article-big.briefing {
    margin: 0;
}

.article-big.briefing div figure {
    height: 23.7rem;
    width: 100px;
}

.article-big.briefing .category {
    width: 100%;
    padding-top: 0.5rem;
    margin-bottom: 0.7rem;
    text-align: center;
    font-size: 1.15rem;
    font: normal bold 1.2rem /1.2 'Noto Sans', sans-serif;
    font: var(--large-category);
    border-bottom: none;
    border-top: 5px solid rgba(255, 188, 0, 1);
    border-top: 5px solid var(--main-yellow);
}

.article-big.briefing .lazyload-wrapper {
    padding-bottom: calc(0.0rem + (55vw));
    padding-bottom: 0.5%;
}

@-webkit-keyframes line {
    from {
        left: 0%;
        width: 0%;
    }
    to {
        left: 0%;
        width: 100%;
    }
}

@keyframes line {
    from {
        left: 0%;
        width: 0%;
    }
    to {
        left: 0%;
        width: 100%;
    }
}

@-webkit-keyframes reverseanim {
    from {
        left: 0%;
        width: 100%;
    }
    to {
        left: 100%;
        width: 0%;
    }
}

@keyframes reverseanim {
    from {
        left: 0%;
        width: 100%;
    }
    to {
        left: 100%;
        width: 0%;
    }
}

.article-big.briefing .category::before {
    position: relative;
    display: block;
    margin-top: -0.8rem;
    content: '';
    border-top: 5px solid black;
    -webkit-animation: reverseanim 0.35s linear both;
            animation: reverseanim 0.35s linear both;
    padding: 0;

}

.article-big.briefing .category:hover::before {
    -webkit-animation: line 0.35s linear forwards;
            animation: line 0.35s linear forwards;
    padding: 0;

}

.article-big.briefing .info {
    margin: 0 1.72rem;
    margin: 0 var(--main-padding);
    text-align: center;
    margin-bottom: 1.9rem;
}

.article-big.briefing h2 {
    margin: 1.4rem 0;
    line-height: 2.0rem;
    text-align: center;
}

.article-big.briefing .summary {
    font-size: 1.1rem;
    line-height: 1.8rem;
    font: normal 1.1rem /1.8rem 'Gelasio', sans-serif;
    font: var(--large-content);
}

.article-big.briefing figure {
    width: 10px;
    height: 10px;
}

.section {
    display: flex;
    flex-direction: column;
    background-image: linear-gradient(#F7F7F7, white);
    background-image: linear-gradient(var(--grey), white);
    padding: 0 1.72rem;
    padding: 0 var(--main-padding);
}

.section:first-of-type div {
    background-image: none;
}

.section .category {
    font: normal bold 1.14rem /1.2 'Noto Sans', sans-serif;
    font: var(--largest-category);
    margin-top: 1.4rem;
    margin-bottom: 1.2rem;
    padding-bottom: 0.2rem;
    border-width: 5px;
}

.card {
    display: grid;
    grid-template-columns: 1fr 2.5fr;
    grid-template-areas: "img title" "img meta";
    padding: 1.4rem 0;
    grid-column-gap: 0.8rem;
    border-bottom: 1px solid #F7F7F7;
    border-bottom: 1px solid var(--grey);
}

.card:last-of-type {}

.card .lazyload-wrapper {
    grid-area: img;
    position: relative;
    align-self: center;
    width: 100%;
    height: 0;
    padding-bottom: 100%;
    justify-content: center;
    overflow: hidden;
    margin-bottom: 0rem;
}

.card img {
    position: absolute;
    left: 50%;
    top: 50%;
    height: 100%;
    width: 100%;
    object-fit: cover;
    transform: translate(-50%, -50%);
}

.card h2 {
    grid-area: title;
    height: auto;
    align-self: end;
    font: bold 1.2rem /1.5rem 'Gelasio', sans-serif;
    font: var(--small-heading);
}

.card .article-meta {
    height: 100%;
    grid-area: meta;
    align-self: end;
    font: italic 0.750rem /1rem 'Gelasio', sans-serif;
    font: var(--small-meta);
}

.nav-footer, .social ul{
    display: flex;
    flex-direction: column;
    flex-wrap: wrap;
    list-style: none;
    height: 10rem;
    border-bottom: 1px solid #F7F7F7;
    border-bottom: 1px solid var(--grey);
    padding: 0 1.72rem;
    padding: 0 var(--main-padding);
    font: normal bold 1.18rem /2.4rem 'Noto Sans', sans-serif;
    font: var(--large-noto);
}

.nav-footer {
    height: 10rem;
    margin-top: 1rem;
    margin-bottom: 1rem;
    border: none;
}

.Subscribe {
    margin: 1.72rem;
    margin: var(--main-padding);
    border-top: 1px solid #F7F7F7;
    border-top: 1px solid var(--grey);
    padding-top: 2rem;
}

.Subscribe .Heading {
    font-family: 'Gelasio', sans-serif;
    width: 5rem;
    margin: 1rem 0rem;
    font: bold 1.85rem /1.9rem 'Gelasio', sans-serif;
    font: var(--medium-heading)
}

.Subscribe .getIt {
    font-family: Noto sans, sans-serif;
    font-size: 1rem;
    font: normal 1rem /2.4rem 'Noto Sans', sans-serif;
    font: var(--small-noto);
    margin: 1rem 0rem;
}

.Subscribe input {
    font: normal 1rem /2.4rem 'Noto Sans', sans-serif;
    font: var(--small-noto);
    font-weight: normal;
    border: none;
    border-bottom: 1px solid black;
    height: 50px;
}

.Subscribe input::-webkit-input-placeholder {
    font: normal 1rem /2.4rem 'Noto Sans', sans-serif;
    font: var(--small-noto);
    color: rgb(180, 180, 180);
}

.Subscribe input:-ms-input-placeholder {
    font: normal 1rem /2.4rem 'Noto Sans', sans-serif;
    font: var(--small-noto);
    color: rgb(180, 180, 180);
}

.Subscribe input::-ms-input-placeholder {
    font: normal 1rem /2.4rem 'Noto Sans', sans-serif;
    font: var(--small-noto);
    color: rgb(180, 180, 180);
}

.Subscribe input::placeholder {
    font: normal 1rem /2.4rem 'Noto Sans', sans-serif;
    font: var(--small-noto);
    color: rgb(180, 180, 180);
}

.Subscribe input:nth-of-type(1), .Subscribe input:nth-of-type(2) {
    width: 50%;
}

.Subscribe input:nth-of-type(3) {
    border: none;
    border-bottom: 1px solid black;
    width: 100%;
}

.Subscribe input:nth-of-type(4) {
    position: relative;
    top: 0;
    border: none;
    border-bottom: 1px solid black;
    width: 100%;
}

.social ul{
    flex-direction: row;
    justify-content: space-between;
    height: auto;
    width: 75%;
    font-size: 1rem;
    border: none;
    padding: 1rem;
    margin: 0 auto;
    font: bold 1rem /1.4rem 'Noto Sans', sans-serif;
    font: var(--small-noto-bold);
}

footer {
    text-align: center;
    font-family: Noto Sans, sans-serif;
    font-size: 0.71rem;
    line-height: 1.4rem;
    letter-spacing: .03rem;
    font: normal 0.71rem /1.4rem 'Noto Sans', sans-serif;
    font: var(--tiny);
}

footer a {
    text-decoration: none;
    color: rgba(255, 188, 0, 1);
    color: var(--main-yellow);
}

.PRR {
    display: flex;
    justify-content: center;
    height: auto;
    width: 100%;
    padding: 1.5rem 2rem;
    background-color: #F7F7F7;
    background-color: var(--grey);
}

.PRR img {
    max-width: 250px;

}

.Loading {
    opacity: 0%;
}
@-webkit-keyframes spinner {
    0% {
      transform: translate3d(-50%, -50%, 0) rotate(0deg);
    }
    100% {
      transform: translate3d(-50%, -50%, 0) rotate(360deg);
    }
  }
@keyframes spinner {
    0% {
      transform: translate3d(-50%, -50%, 0) rotate(0deg);
    }
    100% {
      transform: translate3d(-50%, -50%, 0) rotate(360deg);
    }
  }

.lazyload-wrapper::before, .loadScreen {
    -webkit-animation: 1s linear infinite spinner;
            animation: 1s linear infinite spinner;
    -webkit-animation-play-state: inherit;
            animation-play-state: inherit;
    border: solid 5px rgba(255, 188, 0, 1);
    border: solid 5px var(--main-yellow);
    border-bottom-color: transparent;
    border-left-color: transparent;
    border-radius: 50%;
    content: "";
    height: 40px;
    width: 40px;
    position: absolute;
    top: 50%;
    left: 50%;
    transform: translate3d(-50%, -50%, 0);
    will-change: transform;
}

@media only screen and (min-width: 600px) {
    :root {
        --biggest-category: normal bold 1.4rem /1.2 var(--secondary-font);
        --biggest-heading: bold 3rem /3rem var(--main-font);
        --biggest-content: 1.3rem /1.7rem var(--main-font);
        --biggest-meta: italic 0.9rem var(--main-font);
        --largest-category: normal bold 1.7rem /1.2 var(--secondary-font);
        --large-content: normal 1.2rem /1.8rem var(--main-font);
        --large-category: normal bold 1.5rem /1.2 var(--secondary-font);
        --large-heading: bold 3rem /3rem var(--main-font);
        --small-bold: normal bold 0.9rem /1.2 var(--secondary-font);
        --medium-heading: bold 1.85rem /1.9rem var(--main-font);
        --medium-content: 0.95rem /1.4rem var(--main-font);
        --medium-meta: italic 0.78rem var(--main-font);
        --medium-bold: 1rem;
        --small-heading: bold 1.8rem /2rem var(--main-font);
        --small-meta: italic 0.750rem /1rem var(--main-font);
        --large-noto: normal bold 1.5em /2.6rem var(--secondary-font);
        --small-noto: normal 1.4rem /2.4rem var(--secondary-font);
        --small-noto-bold: bold 1.2rem /1.4rem var(--secondary-font);
        --tiny: normal 0.71rem /1.4rem var(--secondary-font);
        --main-padding: 2.1rem;
    }
    .App-header {
        margin-bottom: 3rem;
    }
    .article-big:first-of-type {
        margin-bottom: 3rem;
    }
    .article-big:first-of-type .lazyload-wrapper {
        padding-bottom: 80%;
    }
    .article-big.briefing {
        margin: 0 1.72rem;
        margin: 0 var(--main-padding);
    }
    .article-big.briefing h2 {
        font: var(--large-heading);
    }
    .article-big.briefing h2, .article-big.briefing .summary, .article-big.briefing .article-meta {
        text-align: left;
    }
    .nav-footer {
        margin-top: 1.8rem;
        height: 12rem;
    }
    .Subscribe {
        margin-top: 0;
        border-top: 1px solid #F7F7F7;
        border-top: 1px solid var(--grey);
        padding-top: 2rem;
    }
    .Subscribe .Heading {
        width: auto;
        font: var(--large-heading)
    }
}

@media only screen and (min-width: 783px) {
    :root {
        --biggest-category: normal bold 1.3rem /1.2 var(--secondary-font);
        --biggest-heading: bold 3.3rem /3.5rem var(--main-font);
        --biggest-content: 1.4rem /1.7rem var(--main-font);
        --biggest-meta: italic 0.9rem var(--main-font);
        --largest-category: normal bold 1.65rem /1.2 var(--secondary-font);
        --large-content: normal 1.2rem /1.8rem var(--main-font);
        --large-category: normal bold 1.3rem /1.2 var(--secondary-font);
        --large-heading: bold 3rem /3rem var(--main-font);
        --small-bold: normal bold 0.9rem /1.2 var(--secondary-font);
        --medium-heading: bold 1.85rem /1.9rem var(--main-font);
        --medium-content: 0.95rem /1.4rem var(--main-font);
        --medium-meta: italic 0.78rem var(--main-font);
        --medium-bold: 1rem;
        --small-heading: bold 1.8rem /2rem var(--main-font);
        --small-meta: italic 0.750rem /1rem var(--main-font);
        --large-noto: normal bold 1.5em /2.6rem var(--secondary-font);
        --small-noto: normal 1.4rem /2.4rem var(--secondary-font);
        --small-noto-bold: bold 1.2rem /1.4rem var(--secondary-font);
        --tiny: normal 0.71rem /1.4rem var(--secondary-font);
        --main-padding: 4.5%;

    }
    .App-header {
        width: 100%;
    }
    .wrapper {
        display: grid;
        grid-template-columns: repeat( auto-fit, minmax(33.33%, 1fr));
        grid-template-rows: repeat(auto-fill, minmax(33.33%, 1fr));
        grid-auto-columns: 1fr;
        margin: 0 1.72rem;
        margin: 0 var(--main-padding);
        grid-gap: 0.6rem;
    }
    .article-big .lazyload-wrapper {
        padding-bottom: 60%;
    }
    .article-big:first-of-type {
        grid-column: 1/4;
        margin: 0;
        margin-bottom: 3rem;
    }
    .article-big:first-of-type .lazyload-wrapper {
        padding-bottom: 56.25%;
    }
    .article-big.briefing {
        grid-row: 3/5;
        grid-column: 2/4;
    }
    .article-big.briefing .lazyload-wrapper {
        height: 0;
        padding-bottom: 60%;
    }
    .article-big.briefing .category {
        margin-top: 0;
        text-align: left;
    }
    .article-big:nth-child(5) {
        grid-row: 2/4;
        grid-column: 3/4;
    }
    .PRR.img-container {
        grid-row: 4/5;
        grid-column: 1/2;
        object-fit: contain;
    }
    .PRR.img-container img {
        width: 100%;
        object-fit: contain;
    }
    /*
.section {
    display: flex;
    flex-direction: column;
    background-image: linear-gradient(var(--grey), white);
    padding: 0 var(--main-padding);
}
*/
    .section {
        margin: 0;
        padding-bottom: 3rem;
    }
    .section:first-of-type {    

    }
    .section .category {
        margin-bottom: 2.5rem;
        margin-top: 4rem;
    }
    .cards-wrapper {
        display: grid;
        grid-template-columns: repeat(3, 1fr);
        grid-gap: 5%;
    }
    .card {
        display: flex;
        flex-direction: column;
        align-items: flex-start;
        padding: 0;
        border: none;
    }
    .card * {
        margin: 0;
    }
    .card:nth-child(4) .lazyload-wrapper, .card:nth-child(5) .lazyload-wrapper, .card:nth-child(6) .lazyload-wrapper {
        display: none;
    }
    .card h2 {
        margin-bottom: 1rem;
        margin-top: 1rem;
    }
    .card .article-meta {
        margin-bottom: 1ch;
        padding: 0;
    }
    .card .lazyload-wrapper {
        width: 100%;
        padding-bottom: 100%;
    }
    .card .lazyload-wrapper img {
        height: 100%;
        width: 100%;
        object-fit: cover;
    }
    .footer-group {
        display: grid;
        grid-template-columns: repeat(2 50px);
        grid-template-rows: repeat(4 auto);
        margin: 0 1.72rem;
        margin: 0 var(--main-padding);
        grid-gap: 1rem   ;
        gap: 1rem   ;
    }
    .App-logo-footer {
        display: block;
        grid-column: 1/2;
        grid-row: 1/2;
    }


    .Subscribe {
        grid-column: 2/3;
        grid-row: 1/3;
        margin: 0;

    }
    .nav-footer-wrapper {
        grid-column: 1/2;
        grid-row: 2/3;
    }
    .social {
        grid-column: 1/2;
        grid-row: 3/4;
    }
    .social ul{
        text-align: left;
        padding: 0;
        margin: 0;
        grid-column: 1/5;
        grid-row: 3/4;
    }
    .social li {
        text-align: left;
        margin-right: 1rem;
    }
    .footer {
        grid-column: 2/3;
        grid-row: 3/4;
        text-align: right;
    }
}

@media only screen and (min-width: 120rem) {
    :root {
        --main-padding: 4.5%;

    }
}
